@import url('https://fonts.googleapis.com/css?family=Cormorant:600');
@font-face {
  font-family: 'Cormorant', serif;
  src: url('https://fonts.googleapis.com/css?family=Cormorant:600');
}
@import url('https://fonts.googleapis.com/css?family=Lora');
@font-face {
  font-family: 'Lora', serif;
  src: url('https://fonts.googleapis.com/css?family=Lora');
}

* {
  /* background: #000 !important;
  color: #0f0 !important;
  outline: solid #f00 1px !important;    */
}

body,
html {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
  -webkit-font-smoothing: antialiased;
  line-height: 0;
}

body .row {
  margin: 0 !important;
}

.bg-0 {
  position: relative;
  background-color: #111;
  padding: 10rem 0 5rem;
}

.introtext-0 {
  font-family: 'Cormorant', serif;
  margin-bottom: 1.5rem;
  width: 770px;
  margin: 5rem auto;
  font-size: 3rem;
  color: whitesmoke;
  opacity: 0.7;
  text-align: center;
  line-height: 70px;
}

.text-container-0 {
  width: 700px;
  margin: 0 auto;
}

.text-0 {
  text-align: justify;
  font-family: 'Lora', serif;
  font-size: 20px;
  color: whitesmoke;
  opacity: 0.7;
  line-height: 1.6;
  margin-bottom: 2rem;
}

.bg-1 {
  position: relative;
  background-color: #c6c2c7;
  padding-top: 150px;
}


.bg-2 {
  position: relative;
  background-color: #c6c2c7;
  padding: 3rem 0 5rem;
}

.introtext-2 {
  font-family: 'Cormorant', serif;
  margin-bottom: 2.5rem;
  margin: 0 auto 2.5rem;
  font-size: 2.5rem;
  width: 700px;
  text-align: center;
}

.text-container-2 {
  width: 700px;
  margin: 0 auto 5rem auto;
  text-align: justify;
  margin-bottom: 0; 
}

.text-2 {
  font-family: 'Lora', serif;
  color: #111;
  line-height: 1.6;
  font-size: 20px;
  text-align: justify;
}

.bg-3 {
  position: relative;
  background-color: #c6c2c7;
  padding: 3rem 0 5rem;
}

.introtext-3 {
  font-family: 'Cormorant', serif;
  margin-bottom: 2.5rem;
  width: 770px;
  margin: 0.5rem auto 2.5rem;
  font-size: 2.5rem;
  text-align: center;
}

.text-container-3 {
  width: 700px;
  margin: 0 auto;
}

.text-3 {
  font-family: 'Lora', serif;
  margin-bottom: 2rem;
  color: #111;
  line-height: 1.6;
  font-size: 20px;
}

.bg-4 {
  position: relative;
  background-color: #a8a8a8;
  padding: 3rem 0 5rem;
}

.bg-5 {
  position: relative;
  background-color: #ffe6cc;
  padding: 3rem 0 5rem;
}

.introtext-4 {
  font-family: 'Cormorant', serif;
  margin-bottom: 2.5rem;
  width: 770px;
  margin: 0.5rem auto 2.5rem;
  font-size: 2.5rem;
  text-align: center;
}

.text-container-4,
.text-container-5,
.text-container-6 {
  width: 700px;
  margin: 0 auto;
  text-align: justify;
}

.text-4,
.text-5 {
  font-family: 'Lora', serif;
  margin-bottom: 2rem;
  color: #111;
  line-height: 1.6;
  font-size: 20px;
}

.bg-6 {
  position: relative;
  background-color: #1c1c1c;
  padding: 3rem 0 4rem;
}

.text-6 {
  font-size: 19px;
  color: whitesmoke;
  opacity: 0.7;
  font-family: 'Lora', serif;
  font-style: italic;
  line-height: 1.5;
}

@media only screen and (max-width: 800px) {
  .bg-3 {
    padding: 3rem 0 1rem;
  }
  .introtext-0 {
    margin: 0rem auto 5.5rem;
  }
  .introtext-2,
  .introtext-3,
  .introtext-4 {
    margin: 0rem auto 3.5rem;
  }
  .introtext-0,
  .introtext-2,
  .introtext-3,
  .introtext-4 {
    width: 70%;
	  font-size: 2rem;
	  line-height: 45px;
  }
  .introtext-1,
  .introtext-2 {
    font-size: 2rem;
	  line-height: 45px;
  }
  .text-container-0,
  .text-container-1,
  .text-container-2,
  .text-container-3,
  .text-container-4,
  .text-container-5,
  .text-container-6 {
    width: 65%;
  }
  .text-0, 
  .text-1,
  .text-1-last,
  .text-2,
  .text-3,
  .text-4,
  .text-5,
  .text-6 {
    font-size: 16px;
  }
}

@media only screen and (max-width: 480px) {
  .StoryboardContent {
    display: none;
  }
}
