.BarGraphContainer-sequence-container {
  -webkit-font-smoothing: antialiased;
  padding: 75px 0 100px;
  background-color: #c6c2c7;
}

.BarGraphContainer-container {
  width: 75%;
  margin: 0 auto;
}

.BarGraphContainer-header-text {
  font-family: 'Cormorant', serif;
  margin-bottom: 1rem;
  text-align: center;
  font-size: 2rem;
}

.BarGraphContainer-header-text-change {
  font-family: 'Cormorant', serif;
  margin-bottom: 0;
  font-size: 2rem;
  height: 2rem;
}

.BarGraphContainer-fadeOut {
  opacity: 0;
  animation: fadeout 0.3s linear;
}

.BarGraphContainer-fadeIn {
  opacity: 1;
  animation: fadein 0.5s linear;
}

@keyframes fadeout {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadein {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@media only screen and (max-width: 800px) {
  .BarGraphContainer-header-text,
  .BarGraphContainer-header-text-change {
    font-size: 1.5rem;
  } 
  .BarGraphContainer-sequence-container {
    padding: 70px 0 50px;
  }
}