.BarGraphContainer-fadeIn {
  opacity: 1;
  -webkit-animation: fadein 0.5s linear;
          animation: fadein 0.5s linear;
}

@-webkit-keyframes fadeout {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes fadeout {
  0% {
    opacity: 1;
